<template>
  <div class="simPancel-smo-model">
    <el-tabs v-model="activeName">
      <el-tab-pane label="Parameters" name="Parameters">
        <div class="top-card top-card-input">
          <div class="fl download-doc-btn">
            <i class="el-icon-document"></i>
            <a :href="user.ftpServerAddress + inputMask" target="_blank">{{inputMask}}</a>
          </div>
          <el-form ref="form" :model="form" label-width="180px" size="mini" class="corenum-input" style="margin-left: 500px;">
            <el-form-item label="Core Number">
              <div>:<span class="paramContent">{{form.coreNum}}</span></div>
            </el-form-item>
          </el-form>
        </div>
        <div class="card">
          <div class="card-header">Mask Parameters</div>
          <div class="card-form">
            <el-form ref="form" :model="form" label-width="180px" size="mini">
              <el-form-item label="Mask Dimension">
                <div>:<span class="paramContent">{{form.maskDimension}}</span></div>
              </el-form-item>
              <el-form-item label="Pixel Size">
                <div>:<span class="paramContent">{{form.pixelSize}}</span></div>
              </el-form-item>
              <el-form-item label="Reflect">
                <div>:<span class="paramContent">{{form.reflect}}</span></div>
              </el-form-item>
              <el-form-item label="Absorb">
                <div>:<span class="paramContent">{{form.absorb}}</span></div>
              </el-form-item>
              <el-form-item label="Shadow Near">
                <div>:<span class="paramContent">{{form.shadowNear}}</span></div>
              </el-form-item>
              <el-form-item label="Shadow Far">
                <div>:<span class="paramContent">{{form.shadowFar}}</span></div>
              </el-form-item>
            </el-form>
          </div>
        </div>
        <div class="card">
          <div class="card-header">Optimization Parameters</div>
          <div class="card-form">
            <el-form ref="form" :model="form" label-width="180px" size="mini">
              <el-form-item label="Step main">
                <div>:<span class="paramContent">{{form.stepMaskMain}}</span></div>
              </el-form-item>
              <el-form-item label="Step SRAF">
                <div>:<span class="paramContent">{{form.stepMaskSraf}}</span></div>
              </el-form-item>
              <el-form-item label="Step source">
                <div>:<span class="paramContent">{{form.stepSource}}</span></div>
              </el-form-item>
              <el-form-item label="Omega mask que">
                <div>:<span class="paramContent">{{form.omegaMaskQua}}</span></div>
              </el-form-item>
              <el-form-item label="Omega source que">
                <div>:<span class="paramContent">{{form.omegaSourceQua}}</span></div>
              </el-form-item>
              <el-form-item label="Maxloop">
                <div>:<span class="paramContent">{{form.maxloopSmo}}</span></div>
              </el-form-item>
              <el-form-item label="Epsilon">
                <div>:<span class="paramContent">{{form.threshold}}</span></div>
              </el-form-item>
              <el-form-item label="Resist Threshold">
                <div>:<span class="paramContent">{{form.tr}}</span></div>
              </el-form-item>
              <el-form-item label="Resist Slope">
                <div>:<span class="paramContent">{{form.aSource}}</span></div>
              </el-form-item>
            </el-form>
          </div>
        </div>
        <div class="card">
          <div class="card-header">Source Parameters</div>
          <div class="card-form">
            <el-form ref="form" :model="form" label-width="180px" size="mini">
              <el-form-item label="Wavelength">
                <div>:<span class="paramContent">{{form.wavelength}}</span></div>
              </el-form-item>
              <el-form-item label="Sigma in">
                <div>:<span class="paramContent">{{form.sigmaIn}}</span></div>
              </el-form-item>
              <el-form-item label="Sigma out">
                <div>:<span class="paramContent">{{form.sigmaOut}}</span></div>
              </el-form-item>
              <el-form-item label="Flare factor">
                <div>:<span class="paramContent">{{form.tis}}</span></div>
              </el-form-item>
              <el-form-item label="NA">
                <div>:<span class="paramContent">{{form.na}}</span></div>
              </el-form-item>
              <el-form-item label="Ratio">
                <div>:<span class="paramContent">{{form.ratio}}</span></div>
              </el-form-item>
            </el-form>
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="Convergence" name="Convergence">
        <div class="top-card">
          <div class="top-card-text">Iteration number:<span>0</span></div>
          <div class="top-card-text">Pattern Error:<span>0</span></div>
        </div>
        <div class="img-card">
          <div class="card-header">Source Pattern</div>
          <div class="notRunning-contain">
            <div class="notRunning">
              <img src="../../assets/images/result.png" alt="result" class="result-icon">
              <span>点击 Start 后可查看运行结果</span>
            </div>
          </div>
        </div>
        <div class="img-card">
          <div class="card-header">Mask Pattern</div>
          <div class="notRunning-contain">
            <div class="notRunning">
              <img src="../../assets/images/result.png" alt="result" class="result-icon">
              <span>点击 Start 后可查看运行结果</span>
            </div>
          </div>
        </div>
        <div class="img-card">
          <div class="card-header">Print Image</div>
          <div class="notRunning-contain">
            <div class="notRunning">
              <img src="../../assets/images/result.png" alt="result" class="result-icon">
              <span>点击 Start 后可查看运行结果</span>
            </div>
          </div>
        </div>
        <div class="img-card">
          <div class="card-header">Convergence Curve</div>
          <div class="notRunning-contain">
            <div class="notRunning">
              <img src="../../assets/images/result.png" alt="result" class="result-icon">
              <span>点击 Start 后可查看运行结果</span>
            </div>
          </div>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
  export default {
    name: 'smo',
    props: ["user"],
    created: function () {
      this.$store.state.adminleftnavnum="/simPancel";
    },
    mounted: function () {
      this.getSmoParam();
    },
    data () {
      return {
        activeName: this.$route.query.type,
        inputMask: '',
        form: {
          maskDimension: '',
          pixelSize: '',
          reflect: '',
          absorb: '',
          shadowNear: '',
          shadowFar: '',
          stepMaskMain: '',
          stepMaskSraf: '',
          stepSource: '',
          omegaMaskQua: '',
          omegaSourceQua: '',
          maxloopSmo: '',//150
          threshold: '',
          tr: '',
          aSource: '',
          wavelength: '',
          sigmaIn: '',
          sigmaOut: '',
          tis: '',
          na: '',
          ratio: '',
          coreNum: ''
        }
      }
    },
    methods: {
      getSmoParam() {
        this.$http.post('api/get_record_detail.do',{
          moduleName: (this.$route.path.split('/')[this.$route.path.split('/').length - 1]).replace('View',''),
          parametersId: this.$route.query.parametersId,
          resultId: this.$route.query.resultId
        }).then((res) => {
          this.form = res.data.data.gdParameterSmo;
          this.inputMask = res.data.data.gdParameterSmo.inputMask;
        }, (err) => {
          this.$message({
            message: '访问数据失败，请检查网络环境！',
            type: 'error',
            duration: 1500,
            showClose: true
          });
        });
      }
    },
    watch: {
      '$route' (to, from) {
        if(to.path === '/simPancel/smoView'){
          this.activeName = this.$route.query.type;
          this.getSmoParam();
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
  .simPancel-smo-model{
    margin: 0 20px 130px 15px;
    .el-form-item--mini.el-form-item, .el-form-item--small.el-form-item{
      margin-bottom: 10px;
    }
    .corenum-input .el-form-item--mini.el-form-item, .el-form-item--small.el-form-item{
      margin-bottom: 0;
    }
    .paramContent{
      margin-left: 10px;
    }
    .card-header{
      font-size: 16px;
      line-height: 18px;
      border-bottom: 1px solid #ebeef5;
      padding: 15px 17px;
      color: #20a0ff;
    }
    .card{
      background-color: #fff;
      box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
      min-height: 100px;
      width: calc(~"33% - 20px");
      margin: 0 20px 0 0;
      float: left;
      .card-form{
        padding: 15px;
      }
    }
    .top-card{
      background-color: #fff;
      box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
      width: 950px;
      padding: 15px;
      overflow: hidden;
      .corenum-input{
        margin-left: 200px;
        max-width: 300px;
      }
      .download-doc-btn{
        line-height: 28px;
        a{
          color: #20a0ff;
          margin-left: 3px;
        }
        a:hover{
          text-decoration: underline;
        }
      }
      .top-card-text{
        float: left;
        line-height: 32px;
        width: 300px;
        margin-left: 20px;
        span{
          color: #20a0ff;
          margin-left: 10px;
          font-weight: bold;
        }
      }
    }
    .top-card-input{
      width: 1053px;
      margin-bottom: 20px;
    }
    .img-card{
      background-color: #fff;
      box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
      width: 480px;
      height: 480px;
      float: left;
      margin: 20px 20px 0 0;
      .result-icon{
        width: 150px;
        vertical-align: middle;
      }
      .notRunning-contain{
        display: flex;
        align-items: center;
        justify-content: center;
        height: 400px;
        .notRunning{
          text-align: center;
          flex: 1;
          height: 120px;
          color: #cdcdcd;
        }
      }
    }
  }
</style>



