<template>
  <div class="simPancel-smo-model">
    <el-tabs v-model="activeName">
      <el-tab-pane label="Parameters" name="Parameters">
        <div class="start">
          <el-button type="primary" icon="el-icon-upload2" @click="startSim();">Start</el-button>
        </div>
        <div class="top-card top-card-input">
          <div class="fl">
            <el-upload class="upload" action="api/upload.do"
                       :on-success="handleUploadSuccess"
                       :before-upload="beforeUpload"
                       :before-remove="beforeRemove"
                       :limit="1">
              <el-button size="small" type="primary">Initial Mask Pattern</el-button>
            </el-upload>
          </div>
          <el-form ref="form" :model="form" label-width="180px" size="mini" class="corenum-input" style="margin-left: 500px;">
            <el-form-item label="Core Number">
              <el-input v-model="form.cn"></el-input>
            </el-form-item>
          </el-form>
        </div>
        <div class="card">
          <div class="card-header">Mask Parameters</div>
          <div class="card-form">
            <el-form ref="form" :model="form" label-width="180px" size="mini">
              <el-form-item label="Mask Dimension">
                <el-input v-model="form.md"></el-input>
              </el-form-item>
              <el-form-item label="Pixel Size">
                <el-input v-model="form.ps"></el-input>
              </el-form-item>
              <el-form-item label="Reflect">
                <el-input v-model="form.re"></el-input>
              </el-form-item>
              <el-form-item label="Absorb">
                <el-input v-model="form.ab"></el-input>
              </el-form-item>
              <el-form-item label="Shadow Near">
                <el-input v-model="form.sn"></el-input>
              </el-form-item>
              <el-form-item label="Shadow Far">
                <el-input v-model="form.sf"></el-input>
              </el-form-item>
            </el-form>
          </div>
        </div>
        <div class="card">
          <div class="card-header">Optimization Parameters</div>
          <div class="card-form">
            <el-form ref="form" :model="form" label-width="180px" size="mini">
              <el-form-item label="Step main">
                <el-input v-model="form.sm"></el-input>
              </el-form-item>
              <el-form-item label="Step SRAF">
                <el-input v-model="form.ssr"></el-input>
              </el-form-item>
              <el-form-item label="Step source">
                <el-input v-model="form.sso"></el-input>
              </el-form-item>
              <el-form-item label="Omega mask que">
                <el-input v-model="form.omq"></el-input>
              </el-form-item>
              <el-form-item label="Omega source que">
                <el-input v-model="form.osq"></el-input>
              </el-form-item>
              <el-form-item label="Maxloop">
                <el-input v-model="form.ma"></el-input>
              </el-form-item>
              <el-form-item label="Epsilon">
                <el-input v-model="form.ep"></el-input>
              </el-form-item>
              <el-form-item label="Resist Threshold">
                <el-input v-model="form.rt"></el-input>
              </el-form-item>
              <el-form-item label="Resist Slope">
                <el-input v-model="form.rs"></el-input>
              </el-form-item>
            </el-form>
          </div>
        </div>
        <div class="card">
          <div class="card-header">Source Parameters</div>
          <div class="card-form">
            <el-form ref="form" :model="form" label-width="180px" size="mini">
              <el-form-item label="Wavelength">
                <el-input v-model="form.wa"></el-input>
              </el-form-item>
              <el-form-item label="Sigma in">
                <el-input v-model="form.si"></el-input>
              </el-form-item>
              <el-form-item label="Sigma out">
                <el-input v-model="form.so"></el-input>
              </el-form-item>
              <el-form-item label="Flare factor">
                <el-input v-model="form.ff"></el-input>
              </el-form-item>
              <el-form-item label="NA">
                <el-input v-model="form.na"></el-input>
              </el-form-item>
              <el-form-item label="Ratio">
                <el-input v-model="form.ra"></el-input>
              </el-form-item>
            </el-form>
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="Convergence" name="Convergence">
        <div class="top-card">
          <div class="top-card-text">Iteration number:<span>0</span></div>
          <div class="top-card-text">Pattern Error:<span>0</span></div>
        </div>
        <div class="img-card">
          <div class="card-header">Source Pattern</div>
          <div class="notRunning-contain">
            <div class="notRunning">
              <img src="../../assets/images/result.png" alt="result" class="result-icon">
              <span>点击 Start 后可查看运行结果</span>
            </div>
          </div>
        </div>
        <div class="img-card">
          <div class="card-header">Mask Pattern</div>
          <div class="notRunning-contain">
            <div class="notRunning">
              <img src="../../assets/images/result.png" alt="result" class="result-icon">
              <span>点击 Start 后可查看运行结果</span>
            </div>
          </div>
        </div>
        <div class="img-card">
          <div class="card-header">Print Image</div>
          <div class="notRunning-contain">
            <div class="notRunning">
              <img src="../../assets/images/result.png" alt="result" class="result-icon">
              <span>点击 Start 后可查看运行结果</span>
            </div>
          </div>
        </div>
        <div class="img-card">
          <div class="card-header">Convergence Curve</div>
          <div class="notRunning-contain">
            <div class="notRunning">
              <img src="../../assets/images/result.png" alt="result" class="result-icon">
              <span>点击 Start 后可查看运行结果</span>
            </div>
          </div>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
  export default {
    name: 'smo',
    created: function () {
      this.$store.state.adminleftnavnum="/simPancel";
    },
    mounted: function () {
    },
    data () {
      return {
        activeName: 'Parameters',
        inputMask: '',
        form: {
          md: 201,
          ps: 11,
          re: 0.8,
          ab: 0.0707,
          sn: 1.424,
          sf: 1.835,
          sm: 0.1,
          ssr: 0.1,
          sso: 0.03,
          omq: 0.0005,
          osq: 0.001,
          ma: 1,//150
          ep: 100,
          rt: 0.25,
          rs: 25,
          wa: 13.5,
          si: 0.24,
          so: 0.84,
          ff: 0.169,
          na: 0.25,
          ra: 4,
          cn: 4
        }
      }
    },
    methods: {
      startSim() {
        if(this.inputMask != ''){
          this.$http.post('api/smo_simulate.do',{
            maskDimension: this.form.md,
            pixelSize: this.form.ps,
            reflect: this.form.re,
            absorb: this.form.ab,
            shadowNear: this.form.sn,
            shadowFar: this.form.sf,
            wavelength: this.form.wa,
            sigmaIn: this.form.si,
            sigmaOut: this.form.so,
            tis: this.form.ff,
            na: this.form.na,
            ratio: this.form.ra,
            stepSource: this.form.sso,
            omegaSourceQua: this.form.osq,
            stepMaskMain: this.form.sm,
            stepMaskSraf: this.form.ssr,
            omegaMaskQua: this.form.omq,
            maxloopSmo: this.form.ma,
            threshold: this.form.ep,
            tr: this.form.rt,
            aSource: this.form.rs,
            coreNum: this.form.cn,
            inputMask: this.inputMask
          }).then((res) => {
            this.activeName = 'Convergence';
            this.$message({
              message: res.data.msg,
              type: res.data.success?'success':'error',
              duration: 1500,
              showClose: true
            });
          }, (err) => {
            this.$message({
              message: '启动smo仿真失败，请检查网络环境！',
              type: 'error',
              duration: 1500,
              showClose: true
            });
          });
        }else{
          this.$message({
            message: '请先上传.mat文件！',
            type: 'warning',
            duration: 1500,
            showClose: true
          });
        }
      },
      handleUploadSuccess(res) {
        if(res.success){
          this.$message({
            message: '文件上传至服务器成功！',
            type: 'success',
            duration: 1500,
            showClose: true
          });
        }else{
          this.$message({
            message: '文件上传失败，请检查网络环境！',
            type: 'error',
            duration: 1500,
            showClose: true
          });
        }
        this.inputMask = res.data.uri;
      },
      beforeUpload(file){
        const fileName = file.name;
        console.log((fileName.split('.')[fileName.split('.').length - 1]));
        const isMat = (fileName.split('.')[fileName.split('.').length - 1]).toLowerCase() === 'mat';
        const isLt10M = file.size / 1024 / 1024 < 10;

        if (!isMat) {
          this.$message.error('上传文件只能是 .mat 格式!');
        }
        if (!isLt10M) {
          this.$message.error('上传文件大小不能超过 10MB!');
        }
        return isMat && isLt10M;
      },
      beforeRemove(file, fileList) {
        this.inputMask = '';
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
  .simPancel-smo-model{
    margin: 0 20px 130px 15px;
    .start{
      margin-bottom: 15px;
    }
    .el-form-item--mini.el-form-item, .el-form-item--small.el-form-item{
      margin-bottom: 10px;
    }
    .corenum-input .el-form-item--mini.el-form-item, .el-form-item--small.el-form-item{
      margin-bottom: 0;
    }
    .card-header{
      font-size: 16px;
      line-height: 18px;
      border-bottom: 1px solid #ebeef5;
      padding: 15px 17px;
      color: #20a0ff;
    }
    .card{
      background-color: #fff;
      box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
      min-height: 100px;
      width: calc(~"33% - 20px");
      margin: 0 20px 0 0;
      float: left;
      .card-form{
        padding: 15px;
      }
    }
    .top-card{
      background-color: #fff;
      box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
      width: 950px;
      padding: 15px;
      overflow: hidden;
      .corenum-input{
        margin-left: 200px;
        max-width: 300px;
      }
      .top-card-text{
        float: left;
        line-height: 32px;
        width: 300px;
        margin-left: 20px;
        span{
          color: #20a0ff;
          margin-left: 10px;
          font-weight: bold;
        }
      }
    }
    .top-card-input{
      width: 1053px;
      margin-bottom: 20px;
    }
    .img-card{
      background-color: #fff;
      box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
      width: 480px;
      height: 480px;
      float: left;
      margin: 20px 20px 0 0;
      .result-icon{
        width: 150px;
        vertical-align: middle;
      }
      .notRunning-contain{
        display: flex;
        align-items: center;
        justify-content: center;
        height: 400px;
        .notRunning{
          text-align: center;
          flex: 1;
          height: 120px;
          color: #cdcdcd;
        }
      }
    }
  }
</style>



