<template>
  <div class="simHistory-model">
    <img src="../../assets/images/bread.png" alt="pancel" class="simPancel-icon">
    <el-breadcrumb separator="/" class="simPancel-breadcrumb">
      <el-breadcrumb-item>仿真历史</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search-group">
      <el-form ref="form" :model="form" label-width="85px">
        <el-form-item label="开始日期">
          <el-col :span="5">
            <el-form-item prop="date1">
              <el-date-picker type="date" placeholder="选择日期" v-model="form.date1" style="width: 100%;"></el-date-picker>
            </el-form-item>
          </el-col>
          <el-col class="line"><div>截止日期</div></el-col>
          <el-col :span="5">
            <el-form-item prop="date2">
              <el-date-picker type="date" placeholder="选择日期" v-model="form.date2" style="width: 100%;"></el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="5">
            <el-input
              class="userNameSearch"
              placeholder="请输入用户名"
              v-model="form.userName"
              clearable>
            </el-input>
          </el-col>
          <el-button type="primary" icon="search" class="query" @click="getSimHistory()">查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="simHistory-download">
      <div class="simHistory-record">
        <span>{{user.name}}</span>，您当前可查看的仿真条数是<span> {{page.total || 0}} </span>条
      </div>
      <!--<el-button type="text" class="download"><i class="el-icon-document"></i>导出数据</el-button>-->
    </div>
    <div class="simHistory-contain">
      <el-table
        :data="simHistoryData"
        v-loading="simHistoryLoading"
        style="width: 100%;box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);">
        <el-table-column
          type="index"
          label="序号"
          min-width="50">
        </el-table-column>
        <el-table-column
          prop="userNo"
          label="学号"
          min-width="140">
        </el-table-column>
        <el-table-column
          prop="userName"
          label="姓名"
          min-width="100">
        </el-table-column>
        <el-table-column
          prop="phone"
          label="手机"
          min-width="140">
        </el-table-column>
        <el-table-column
          prop="startTime"
          label="仿真开始时间"
          min-width="180">
        </el-table-column>
        <el-table-column
          prop="endTime"
          label="仿真结束时间"
          min-width="180">
        </el-table-column>
        <el-table-column
          prop="moduleName"
          label="仿真类型"
          min-width="100">
        </el-table-column>
        <el-table-column
          prop="parameter"
          min-width="100"
          label="仿真参数">
          <template slot-scope="scope">
            <router-link :to="{ path: '/simPancel/' + scope.row.moduleName + 'View' , query: { parametersId: scope.row.parametersId, resultId: scope.row.resultId, type: 'Parameters'}}">
              <span class="simHistory-link">查看参数</span>
            </router-link>
          </template>
        </el-table-column>
        <el-table-column
          prop="result"
          label="仿真结果"
          min-width="100">
          <template slot-scope="scope">
            <router-link :to="{ path: '/simPancel/' + scope.row.moduleName + 'View' , query: { parametersId: scope.row.parametersId, resultId: scope.row.resultId, type: 'Convergence'}}">
              <span class="simHistory-link">查看结果</span>
            </router-link>
          </template>
        </el-table-column>
        <el-table-column
          v-if="false"
          prop="parametersId"
          label="parametersId">
        </el-table-column>
        <el-table-column
          v-if="false"
          prop="resultId"
          label="resultId">
        </el-table-column>
      </el-table>
      <div class="pagination">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="page.currentPage"
          :page-size="page.pageSize"
          layout="prev, pager, next, jumper"
          :total="page.total">
        </el-pagination>
      </div>

    </div>
  </div>
</template>

<script>
  export default {
    name: 'simHistory',
    props: ["user"],
    created: function () {
      this.getSimHistory();
      this.$store.state.adminleftnavnum="/simHistory";
    },
    methods: {
      //分页
      handleSizeChange(val) {
        this.page.currentPage = 1;
      },
      handleCurrentChange(val) {
        this.page.currentPage = val;
        this.getSimHistory();
      },
      getSimHistory() {
        this.$http.post('api/get_all_simulation_records.do',{
          startTime: this.form.date1,
          endTime: this.form.date2,
          userName: this.form.userName,
          pageNum: this.page.currentPage,
          pageSize: this.page.pageSize
        }).then((res) => {
          this.simHistoryData = res.data.data.list;
          this.page.total = res.data.data.total;
          this.simHistoryLoading = false;
        }, (err) => {
          this.$message({
            message: '读取仿真历史失败，请检查网络环境！',
            type: 'error',
            duration: 1500,
            showClose: true
          });
        });
      },
    },
    data () {
      return {
        page: {
          currentPage: 1,
          pageSize: 10,
          total: 0
        },
        form: {
          date1: '',
          date2: '',
          userName: ''
        },
        simHistoryData: [],
        simHistoryLoading: true
      }
    },
    watch: {
      '$route' (to, from) {
        if(to.path === '/simHistory'){
          this.getSimHistory();
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less">
  .simHistory-contain{
    margin: 10px 15px 130px 15px;
  }
  .simHistory-download{
    margin-right: 15px;
    overflow: hidden;
    .simHistory-record{
      float: left;
      line-height: 34px;
      margin-left: 15px;
      span{
        font-weight: bold;
        color: #20a0ff;
      }
    }
    .download{
      float: right;
    }
    .el-icon-document{
      margin-right: 5px;
    }
  }
  .search-group{
    .line{
      width: 10%;
      div{
        text-align: right;
        padding-right: 12px;
      }
    }
    .el-form-item{
      margin-bottom: 5px;
    }
    .userNameSearch{
      margin-left: 30px;
    }
    .query{
      margin-left: 60px;
    }
  }
  .pagination{
    float: right;
    margin: 10px 0 10px 0;
    overflow: hidden;
  }
  .simHistory-link{
    color: #20a0ff;
  }
</style>



